const ProfileMy = (props) => {
  const hide = false;
  return (
    <section className="main-container f-info">
      {/* <div className="f-info__header">
        <div className="f-info__header-left">
          <div className="f-info__svg">
            <InfoIcon />
          </div>
          <p className="f-info__title"></p>
        </div>
      </div> */}
      <div className="f-info__header">
        <div className="f-info__header-left">
          <div className="fr__media-ln-inn">
            <div className="f-info__header-left">
              <NavLink
                exact
                to="/profile"
                activeClassName="active"
                className="f-info__header__item"
              >
                <div className="f-info__svg">
                  <InfoIcon />
                </div>
                <p className="f-info__title"></p>
              </NavLink>
              {!props.location.pathname.includes('/profile/edit') ? null : (
                <div className="fr__pets-ln">
                  <NavLink
                    exact
                    to="/profile/edit"
                    activeClassName="active"
                    className="f-info__header__item"
                  >
                    <p className="f-info__title">
                      <span className="fr__pets-ln-arr">
                        <FArrowIcon />
                      </span>
                      
                    </p>
                    <span
                      style={{ backgroundColor: '#7c7474' }}
                      className="f-info__line"
                    ></span>
                  </NavLink>
                </div>
              )}
            </div>
          </div>
        </div>
        <div className="f-info__header-right">
          {props.location.pathname === '/profile' ? (
            <NavLink to="/profile/edit">
              <ButtonBrown text="" />
            </NavLink>
          ) : null}
          {props.location.pathname === '/profile/edit' ? (
            <NavLink to="/profile">
              <ButtonBrown text="" />
            </NavLink>
          ) : null}
        </div>
      </div>
      {hide ? (
        <NonePageButtonOne
          button={ButtonBrown}
          buttonText=" "
          UpperText="     "
        />
      ) : (
        <Switch>
          <Route exact path="/profile" render={() => <ProfileMyInfo />} />
          <Route path="/profile/edit" render={() => <ProfileMyEdit />} />
        </Switch>
        // <> {editMode ? <ProfileMyEdit /> : <ProfileMyInfo />} </>
      )}
    </section>
  );
};
