import React from 'react';
import '../../../scss/App.scss';
import SideBarFriends from '../../sideBars/SideBarFriends';
import { Route, Switch, withRouter } from 'react-router-dom';
import SideBarSearch from '../../sideBars/SideBarSearch';
import { WithAuthRedirect } from '../../hoc/WithAuthRedirect';
const Search = (props) => {
  return (
    <div>
      <div className="app__main">
        <SideBarSearch location={props.location} />
        <div className="app__main-right">
          <Switch>
            <Route path="/search/all" render={() => <h1></h1>} />
            <Route path="/search/news" render={() => <h1></h1>} />
            <Route path="/search/people" render={() => <h1></h1>} />
            <Route path="/search/groups" render={() => <h1></h1>} />
            <Route path="/search/media" render={() => <h1> </h1>} />
            <Route path="/search/services" render={() => <h1></h1>} />
            <Route path="/search/products" render={() => <h1></h1>} />
            <Route
              path="/search/library"
              render={() => <h1></h1>}
            />{' '}
            <Route
              path="/search/messages"
              render={() => <h1> </h1>}
            />
            <Route path="/search/chats" render={() => <h1></h1>} />
            <Route path="/search/forums" render={() => <h1></h1>} />
          </Switch>
        </div>
      </div>
    </div>
  );
};
const redSearch = WithAuthRedirect(Search);
const WrapperdSearch = withRouter(redSearch);

export default WrapperdSearch;
